
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { push } from 'react-router-redux';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';


import './Login.css';
import Loader from '../../../common/Components/Loader';
import { WEB_TOKEN, USER_DETAILS, APP_NAME } from '../../../constants/otherConstants';
import agent from '../../../agent';
import { SET_USER } from '../../../constants/actionTypes';
import { store } from '../../../store';

import Logo from '../../../common/Components/Logo';


class Login extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: {
        username: '',
        password: '',
      },
      loading: false,
      error: '',
      showPassword:true,

    };
  }

  componentWillMount() {
    if (this.props.user) this.setState({ user: this.props.user.user });
  }

  login(e) {
    e.preventDefault();
    this.setState({ loading: true, error: '' });
    const { email ,password } = this.state.user
    const data = {username:email, password};

    agent.Auth.login(data).then((res) => {
      agent.setToken(res.data.token);
      window.localStorage.setItem(WEB_TOKEN, res.data.token);
      console.log(res);
      // window.localStorage.setItem(USER_DETAILS, res.data.user.username);
      this.props.setUser({ user: res.data.user });
      store.dispatch(push('/admin'));
      this.setState({ loading: false });
    }).catch((err) => {
      this.setState({ loading: false });
      console.log(err);
      if (err.response) {
        this.setState({ error: err.response.data.error, loading: false });
      }
    });
  }

  render() {
    const { email, password } = this.state.user;
    const { showPassword,user } = this.state;
    return (
      <div className="auth-div center">
        <article className="center  ">
          <div>
            <div>
              <div className="tc box vertical-align " style={{ position: 'relative' }}>
                <Logo
                  logostyles={{
                    display: 'flex', flexDirection: 'row', maxWidth: '200px', margin: 'auto',
                  }}
                  parent="login"
                />
                <form onSubmit={this.login.bind(this)}>
                  <div className="input-label-div-login">
                    <input
                      type="text"
                      name="phone-number"
                      className="inp"
                      value={email}
                      placeholder=" "
                      onChange={(e) => this.setState({ user: { ...user, email: e.target.value } })}
                      required
                    />
                    <label className="inp-label">
                      Email
                    </label>
                  </div>
                  <div className="input-label-div-login">
                    <input
                      type={showPassword?"text":"password"}
                      name="phone-number"
                      className="inp"
                      required
                      value={password}
                      placeholder=" "

                      onChange={(e) => this.setState({ user: { ...user, password: e.target.value.toLowerCase() } })}
                    />
                    <label className="inp-label">
                      password
                    </label>
                    <button type="button" onClick={()=> this.setState({showPassword:!showPassword})} >{showPassword ? <VisibilityOffIcon style={{color:'white'}} /> : <VisibilityIcon style={{color:'white'}}/> } </button>
                  </div>
                  <p className="err-txt">{this.state.error}</p>
                  <input type="submit" value="Login" className="login-btn w-100" />
                </form>
                <a href="/forgot-password" className="mt2 ">
                  Forgot password
                </a>
              </div>

              <button
                onClick={() => store.dispatch(push('/signup'))}
                className="btn ba w-100 ml0 "
                type="button"
                style={{
                  marginLeft: 0, marginTop: '1rem', border: 'solid 1px rgba(0,0,0,0.5)', padding: '10px',
                }}
              >
            Don't have an account? Sign up
              </button>

              {
                this.state.loading
                  ? (
                    <Loader />
                  ) : null
              }
            </div>
          </div>

        </article>
      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user : state.AuthReducer.user
});

const mapDispatchToProps = (dispatch) => ({
  setUser: (user) => dispatch({ type: SET_USER, payload: user }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Login);
