/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import StyleIcon from '@material-ui/icons/Style';
import Slide from '@material-ui/core/Slide';

import './styles.css';
import { toast } from 'react-toastify';
import { GET_HEADER_INFO, GET_BG_STYLE } from '../../../constants/actionTypes';

import Loader from '../../../common/Components/Loader';
import DisabledDiv from '../../../common/Components/DisabledDiv';
import SelectImage from '../../../common/Components/SelectImage';
import { DEFAULT_BG_STYLE } from '../../../constants/otherConstants';
import agent from '../../../agent';

const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);
const themes = [
  {
    ...JSON.parse(DEFAULT_BG_STYLE),
  },
  {
    backgroundColor: '#861657',
    backgroundImage: 'linear-gradient(326deg, #861657 0%, #ffa69e 74%)',

  },
  {
    backgroundColor: '#bd4f6c',
    backgroundImage: 'linear-gradient(326deg, #bd4f6c 0%, #d7816a 74%)',
  },
  {
    backgroundColor: '#a4508b',
    backgroundImage: 'linear-gradient(326deg, #a4508b 0%, #5f0a87 74%)',
  },
  {
    backgroundColor: '#a40606',
    backgroundImage: 'linear-gradient(315deg, #a40606 0%, #d98324 74%)',
  },
  {
    backgroundColor: '#7ee8fa',
    backgroundImage: 'linear-gradient(315deg, #7ee8fa 0%, #80ff72 74%)',

  },
  {
    backgroundColor: '#eec0c6',
    backgroundImage: 'linear-gradient(315deg, #eec0c6 0%, #7ee8fa 74%)',

  },
  {
    backgroundColor: '#fd8f67',
    background: 'linear-gradient(0deg,#fd8f67,#d463a3)',
    backgroundImage: 'linear-gradient(0deg, rgb(253, 143, 103), rgb(212, 99, 163))',
  },
  {
    backgroundColor: '#9fa4c4',
    backgroundImage: 'linear-gradient(315deg, #9fa4c4 0%, #9e768f 74%)',

  },
  {
    backgroundColor: '#6b0f1a',
    backgroundImage: 'linear-gradient(315deg, #6b0f1a 0%, #b91372 74%)',

  },
  {
    backgroundColor: '#e9bcb7',
    backgroundImage: 'linear-gradient(315deg, #e9bcb7 0%, #29524a 74%)',
  },
  {
    backgroundColor: '#90d5ec',
    backgroundImage: 'linear-gradient(315deg, #90d5ec 0%, #fc575e 74%)',

  },
  {
    backgroundColor: '#d5adc8',
    backgroundImage: 'linear-gradient(315deg, #d5adc8 0%, #ff8489 74%)',
  },
  {
    backgroundColor: '#fff',
    backgroundImage: 'linear-gradient(315deg, #3f0d12 0%, #a71d31 74%)',
  }, {
    backgroundColor: '#3f0d12',
    backgroundImage: 'linear-gradient(315deg, #3f0d12 0%, #a71d31 74%)',
  },
];
class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      loading: false,
      error: '',
      bgStyle: JSON.parse(DEFAULT_BG_STYLE),
      isNormalUser: true,
      changeThemeOpen: false,
      user: {},

    };
    this.handleChangeThemeDialogOpen = this.handleChangeThemeDialogOpen.bind(this);
  }


  componentWillMount() {
    if (this.props.user) {
      const temp = this.props.user.user.user_type === 'normal';
      this.setState({ user: this.props.user.user, isNormalUser: temp });
    }
    if (this.props.selectedStyle) {
      if (this.props.selectedStyle.data) {
        this.setState({ selectedStyle: this.props.selectedStyle.data });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (np.user && np.user.user !== this.state.user) {
      const temp = np.user.user.user_type === 'normal';

      this.setState({ user: np.user.user, isNormalUser: temp });
    }
    if (np.selectedStyle && np.selectedStyle.background_color !== this.state.selectedStyle) {
      this.setState({ selectedStyle: np.selectedStyle.background_color });
    }
  }

  handleChangeThemeDialogOpen() {
    this.setState({ changeThemeOpen: !this.state.changeThemeOpen });
  }

  onSelectFile(file) {
    this.setState({ loading: true });

    console.log(file);
    const { blob} = file;
    let data =[
      {key:"background_color",file:this.state.selectedStyle},
      {key:"photo",file:blob},
    ]
    console.log(data)
    agent.AdminPage.uploadBackground(data).then((res) => {
      console.log(res);
      const { username } = this.state.user;
      const data = { username };
      this.props.getBackgroundStyle(data);
      this.setState({ loading: false, changeThemeOpen: false });
      //   toast.error('error in uploading background !!!');
    }).catch((err) => {
      this.setState({ loading: false });
      toast.error('error in uploading background !!!');
    });
  }


  selectBg(bg) {
    this.setState({ loading: true });
    // const data = {
    //   background_color: JSON.stringify(bg),
    // };
    let data =[
      {key:"background_color",file:JSON.stringify(bg)},
      {key:"photo",file:null},
    ]
    // console.log(data)
    agent.AdminPage.uploadBackground(data).then((res) => {
      console.log(res);
      const { username } = this.state.user;
      const data = { username };
      this.props.getBackgroundStyle(data);
      this.setState({ loading: false, changeThemeOpen: false });
      //   toast.error('error in uploading background !!!');
    }).catch((err) => {
      this.setState({ loading: false });
      toast.error('error in uploading background !!!');
    });
    // this.props.setTempHomeBg(bg);
  }

  renderThemes() {
    return themes.map((t, i) => {
      if (this.state.selectedStyle === JSON.stringify(t)) {
        return (
          <button type="button" onClick={() => this.selectBg(t)} key={i} className="change-bg-theme active-theme" style={t} key={i} />
        );
      }
      return (
        <button type="button" onClick={() => this.selectBg(t)} key={i} className="change-bg-theme" style={t} />
      );
    });
  }

  
  render() {
    return (
      <div className="justify-center self-center mt2  center">
        <div  className="side-drawer-link relative" onClick={() => this.handleChangeThemeDialogOpen()}>
          <StyleIcon />
            Choose Theme

        </div>
        <Dialog
          open={this.state.changeThemeOpen}
          TransitionComponent={Transition}
          keepMounted
          onClose={this.handleChangeThemeDialogOpen}
          aria-labelledby="alert-dialog-slide-title"
          aria-describedby="alert-dialog-slide-description"

        >

          <DialogTitle id="alert-dialog-slide-title">Choose Theme</DialogTitle>
          <div className='w-100 relative flex ' >
          <button className="change-bg-image-btn relative "> Choose Custom Background Image <SelectImage onSelectFile={this.onSelectFile.bind(this)} isCroppingRequired={true} /> </button>
          {
              this.state.isNormalUser?<DisabledDiv /> : null
            }
          </div>
          <div className="flex w-100 justify-center items-center relative">
            <div className="line-div" />
            <p className="b f3">OR</p>
            <div className="line-div" />
            
          </div>

          <DialogContent style={{ flexWrap: 'wrap', position: 'relative' }}>
            <div className="flex flex-wrap center justify-center items-center">
              {
                    this.renderThemes()
                }
            </div>

          </DialogContent>
          {
            this.state.loading ? <Loader /> : null
          }
        </Dialog>


      </div>

    );
  }
}

const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  selectedStyle: state.AdminPageReducers.homeBgStyle,
  headerInfo: state.AdminPageReducers.headerInfo,
});

const mapDispatchToProps = (dispatch) => ({
  getHeaderInfo: (user) => dispatch({ type: GET_HEADER_INFO, payload: agent.AdminPage.getHeaderInfo(user) }),
  getBackgroundStyle: (user) => dispatch({ type: GET_BG_STYLE, payload: agent.AdminPage.getBackground(user) }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
